@font-face {
  font-family: "Golos Text";
  src: url("../fonts/Golos/GolosText-DemiBold.eot");
  src: local("Golos Text DemiBold"), local("GolosText-DemiBold"), url("../fonts/Golos/") format("embedded-opentype"), url("../fonts/Golos/GolosText-DemiBold.woff2") format("woff2"), url("../fonts/Golos/GolosText-DemiBold.woff") format("woff"), url("../fonts/Golos/GolosText-DemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Golos Text";
  src: url("../fonts/Golos/GolosText-Regular.eot");
  src: local("Golos Text Regular"), local("GolosText-Regular"), url("../fonts/Golos/GolosText-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Golos/GolosText-Regular.woff2") format("woff2"), url("../fonts/Golos/GolosText-Regular.woff") format("woff"), url("../fonts/Golos/GolosText-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body,
h1,
div,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Golos Text", "Arial", sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
}

details > summary {
  list-style: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

a {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  padding: 0;
}

select {
  appearance: none;
}

select:focus,
input:focus,
button:focus {
  outline: none;
  border: none;
}

div {
  outline: none;
}

select::-ms-expand {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  display: flex;
  justify-content: center;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 16px 16px 12px;
}
@media (min-width: 768px) {
  .header__wrapper {
    padding: 24px 90px;
  }
}
@media (min-width: 1200px) {
  .header__wrapper {
    min-width: 1200px;
    max-width: 1200px;
  }
}
.header__desc {
  display: none;
}
@media (min-width: 960px) {
  .header__desc {
    display: block;
    padding-left: 24px;
    font-size: 11px;
    line-height: 17px;
    color: #8f9090;
    max-width: 320px;
    word-break: break-word;
  }
}
.header__left {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__logo-mob {
  width: 129px;
  height: 20px;
}
@media (min-width: 768px) {
  .header__logo-mob {
    display: none;
  }
}
.header__logo-desktop {
  display: none;
}
@media (min-width: 768px) {
  .header__logo-desktop {
    display: block;
    width: 201px;
  }
}
.header__right {
  display: flex;
  gap: 20px;
  align-items: center;
  position: relative;
}
@media (min-width: 768px) {
  .header__right {
    gap: 32px;
  }
}
.header__icon {
  width: 20px;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .header__icon {
    margin-right: 0;
  }
}
.header__link {
  display: none;
}
@media (min-width: 768px) {
  .header__link {
    display: block;
    color: #003154;
    font-size: 14px;
    line-height: 22px;
    text-decoration: none;
  }
}

.mail {
  display: flex;
  justify-content: center;
}
.mail__icon {
  display: none;
}
@media (min-width: 768px) {
  .mail__icon {
    display: block;
    width: 15px;
    margin-right: 6px;
  }
}

.toggle-menu {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  width: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 19px;
  cursor: pointer;
}

.line {
  width: 20px;
  height: 2px;
  border-radius: 3px;
  background-color: #1e1e1e;
  transition: transform 0.2s ease-out;
}

.toggle .line1 {
  background-color: #ffffff;
  transform: scale(0.9) rotateZ(-45deg) translate(-4px, 4px);
}

.toggle .line2 {
  display: none;
}

.toggle .line3 {
  background-color: #ffffff;
  transform: scale(0.9) rotateZ(45deg) translate(-4px, -4px);
}

.toggle .toggle-menu {
  background-color: transparent;
}

.nav {
  display: flex;
}
@media (min-width: 768px) {
  .nav {
    display: none;
  }
}
.nav__name {
  color: #ffffff;
  font-size: 24px;
  text-decoration: none;
  padding-bottom: 15px;
}
.nav__button {
  border: none;
  background-color: transparent;
  box-shadow: none;
}
.nav__overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 49, 84, 0.6);
  top: 0;
  left: 0;
  right: -100%;
  bottom: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s;
}
.nav__content {
  background-color: #174463;
  padding: 42px 25px 25px;
  height: 100%;
  width: 260px;
  position: absolute;
  margin: 0 auto;
  z-index: 99;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  right: -260px;
  transition: all 0.5s;
}
.nav__content--open {
  right: 0;
  transition: all ease 0.5s;
}
.nav__logo {
  width: 210px;
}
.nav__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  padding-top: 30px;
}
.nav__signature {
  width: 110px;
  color: #ffffff;
  font-size: 13px;
}

.overlay-visible {
  visibility: visible;
  opacity: 1;
}

.navigation__wrapper {
  padding: 12px 16px;
}
.navigation__list {
  overflow-x: scroll;
}
.navigation__list-content {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  display: flex;
  gap: 10px;
  max-width: 100%;
}
@media (min-width: 900px) {
  .navigation__list-content {
    justify-content: center;
  }
}
.navigation__item {
  color: #1e1e1e;
  font-size: 14px;
  line-height: 22px;
  padding: 8px 12px;
  background-color: #f7f7f8;
  border-radius: 4px;
  white-space: nowrap;
}

.navigation__list::-webkit-scrollbar {
  display: none;
}

.offer {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.offer__wrapper {
  padding: 24px 16px;
  background: linear-gradient(to bottom, rgb(96, 192, 195) 30%, rgb(247, 247, 248) 30%, rgb(247, 247, 248) 100%, rgb(96, 192, 195) 100%);
}
.offer__image {
  width: 288px;
  height: 216px;
  filter: drop-shadow(4px 8px 24px rgba(0, 0, 0, 0.2)) drop-shadow(16px 28px 64px rgba(0, 0, 0, 0.16));
}
.offer__title {
  font-size: 23px;
  line-height: 25px;
  color: #003154;
  padding-bottom: 8px;
}
.offer__subtitle {
  font-size: 15px;
  line-height: 22px;
  color: #404040;
  padding-bottom: 32px;
}
.offer__link {
  font-size: 15px;
  line-height: 22px;
  color: #003154;
  padding: 12px 38px;
  border: 1px solid #003154;
  border-radius: 2px;
}

.icons {
  margin-bottom: 8px;
}
.icons__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  display: flex;
  gap: 10px;
}
.icons__item {
  min-width: 24px;
  max-width: 24px;
  min-height: 24px;
  max-height: 24px;
}

.icon {
  min-width: 24px;
  max-width: 24px;
  min-height: 24px;
  max-height: 24px;
}

html {
  scroll-behavior: smooth;
}